
<template>
	<div>
		<cinput @woyaobaocun = 'zhendeyaobaocuna' ></cinput>
		<clist @shanchuwo = "zhendeyaoshanchuwo" v-bind:comments="comments"></clist>
	</div>
</template>

<script>
	import cinput from './cInput.vue';
	import clist from './cList.vue';
	
	export default {
		data() {
			return {
				comments:[
					{id:1,author:'潮汐',content:'啦啦啦。'},
					{id:2,author:'卡莎',content:'哈哈哈。。'}
				]
			}
		},
		methods:{
			zhendeyaobaocuna(res){
				this.comments.push(res);	
			},
			zhendeyaoshanchuwo(id){
				this.comments = this.comments.filter((c) => c.id != id);
			}
			
		},
		components:{
			cinput: cinput,
			clist: clist
		}
	}
</script>

