<template>
	<div class="cinput">
		<label class="lable">
			<span>用户名：</span>
			<input v-model="author"/>
		</label>
		<label class="lable">
			<span>评论内容：</span>
			<textarea v-model="content"></textarea>
		</label>
		<button @click="doSave">发布</button>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				author:'',
				content:''
			};
		},
		methods:{
			doSave(){
				if(!this.author){
					return alert("用户名不能为空！");
				}
				if(!this.content){
					return alert("评论不能为空！！！");
				}
				
				this.$emit('woyaobaocun',{
					id: +new Date(),
					author:this.author,
					content:this.content
				});
				
				this.content = '';
			}
		}
	}
	
	
</script>

